.item{
    width: 100%;
    display: flex;
    border: 2px solid var(--border-color);
    border-radius: 5px;
    align-items: center;
    margin-bottom: 15px;
    padding: 15px;
    box-shadow: 0px 0px 10px rgb(161, 161, 161);   
    transition-property: all;
    transition-duration: 300ms;
    transition-timing-function: linear;
    transition-delay: 100ms;
}

.item:hover{
    box-shadow: inset 0px 0px 5px    rgb(246, 237, 237),
                inset 0px 0px 10px rgb(218, 201, 201),
                inset 0px 0px 15px rgb(225, 221, 221);
}
.status{    
    width: 20px;
    height: 20px;
    border-radius: 50%;
    
}

.name{
    font-size: 28px;
    margin-left: 15px;
}

.avatar{
    margin-left: 15px;
}