

.statistics{
    width: 500px;
    height: 200px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    border: 2px solid var(--border-color);
    border-radius: 5px ;
    box-shadow: 0 5px 15px rgb(161, 161, 161);
}

.statistics:hover{
box-shadow: 0 10px 30px rgb(161, 161, 161);
}

.statList{
    display: flex;
}

.title{
    text-align: center;
    padding-top: 30px;
}

.item {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: rgb(245, 244, 244);
    width: 100px;
    height: 100px;
}

.item:not(:last-child) {
