

.profile{
    width: 300px;
    height: 400px;
    border: 2px solid var(--border-color);
    border-radius: 6px;
    font-family: sans-serif;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    box-shadow: 0 5px 15px rgb(161, 161, 161);
}

.profile:hover{
box-shadow: 0 10px 30px rgb(161, 161, 161);
}

.description{
    display: flex;
    flex-direction: column;
    padding: 20px;
    align-items: center;
    height: 100%;    
}

.avatar{
    display: inline-block;
    width: 150px;
    height: 150px;
    border: 2px solid var(--border-color);
    border-radius: 50%;
}

.name{
    font-size: 26px;
    font-weight: 700;
    margin-top: 15px;
}

.tag,
.location{
    margin-top: 10px;
}

.stats{
    width: 100%;
    list-style: none;
    display: flex;
    border-top: 2px solid var(--border-color);
}

.item{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: rgb(245, 244, 244);
    width: 100px;
    height: 100px;
}

.item:not(:last-child){
    border-right: 2px solid var(--border-color) ;
}

.label,
.tag,
.location {
color: grey;
}

.quantity{
    padding-top: 5px;
    font-weight: 700;
}